body {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

h1 {
    margin: 0;
    padding: 0;
}

.container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 4fr;
    grid-template-rows: 70px 1fr;
}

#header {
    grid-column: 1 / 3;
    grid-row: 1 / 2;
    display: flex;
    align-items: center;
    padding-left: 20px;
    background-color: aqua;
}

#nav-bar {
    grid-column: 1 / 2;
    grid-row: 2 /3;
    display: flex;
    flex-direction: column;
    gap: 20px;
    padding: 20px;
}

#nav-bar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#project-container {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

#hidden-project-container {
    display: none;
}

#minimize-button {

}

.project-button {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 8px;
    background-color: transparent;
    border: none;
    border-radius: 5px;
}

.project-title {
    margin: 0;
    padding: 0;
    font-size: 20px;
}

.remove-button{
    /* border: none; */
}

.project-button:hover {
    background-color: rgb(175, 175, 175);
}

#list-container {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    display: flex;
    flex-direction: column;
    gap: 20px;
    background-color: blanchedalmond;
    padding: 20px;
}

#new-project-form {
    display: grid;
    grid-template-columns: 2fr 1fr 2fr;
    grid-template-rows: repeat(3 1fr);
    gap: 20px;
}

#project-name-label {
    grid-template-columns: 1 / 4;
    grid-template-rows: 1 / 2;
}

#project-name-input {
    grid-column: 1 / 4;
    grid-row: 2 / 3;
}

#create-button {
    grid-column: 1 / 2;
    grid-row: 3 / 4;
}

#cancel-button {
    grid-column: 3 / 4;
    grid-row: 3 / 4;
}

#new-project-form {
    display: grid;
    grid-template-columns: repeat(5 1fr);
    grid-template-rows: repeat(2 1fr);
}

#task-name-input {
    width: 20%;
    flex-grow: 0;
}

#new-task-button {
    width: 100px;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;;IAEI,aAAa;IACb,YAAY;IACZ,SAAS;IACT,UAAU;AACd;;AAEA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,aAAa;IACb,8BAA8B;IAC9B,4BAA4B;AAChC;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,sBAAsB;AAC1B;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;;AAEA;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,YAAY;IACZ,6BAA6B;IAC7B,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,SAAS;IACT,UAAU;IACV,eAAe;AACnB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,SAAS;IACT,gCAAgC;IAChC,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,kCAAkC;IAClC,iCAAiC;IACjC,SAAS;AACb;;AAEA;IACI,4BAA4B;IAC5B,yBAAyB;AAC7B;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,oCAAoC;IACpC,iCAAiC;AACrC;;AAEA;IACI,UAAU;IACV,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB","sourcesContent":["html,\nbody {\n    height: 100vh;\n    width: 100vw;\n    margin: 0;\n    padding: 0;\n}\n\nh1 {\n    margin: 0;\n    padding: 0;\n}\n\n.container {\n    height: 100%;\n    width: 100%;\n    display: grid;\n    grid-template-columns: 1fr 4fr;\n    grid-template-rows: 70px 1fr;\n}\n\n#header {\n    grid-column: 1 / 3;\n    grid-row: 1 / 2;\n    display: flex;\n    align-items: center;\n    padding-left: 20px;\n    background-color: aqua;\n}\n\n#nav-bar {\n    grid-column: 1 / 2;\n    grid-row: 2 /3;\n    display: flex;\n    flex-direction: column;\n    gap: 20px;\n    padding: 20px;\n}\n\n#nav-bar-header {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n#project-container {\n    display: flex;\n    flex-direction: column;\n    gap: 10px;\n}\n\n#hidden-project-container {\n    display: none;\n}\n\n#minimize-button {\n\n}\n\n.projec